<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
    
        :src="item.product.image.file.url"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <p class="product__info">Объем: <span>128GB</span></p>
    <span class="product__code">Артикул: {{ item.product.id }}</span>
    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click.prevent="minus"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="item.quantity" name="count" />

      <button
        type="button"
        aria-label="Добавить один товар"
        @click.prevent="plus"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">{{ item.price * item.quantity }}</b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCart(item)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["item"],
  methods: {
    ...mapActions({
      deleteCart: "API/deleteCart",
      updateCart: "API/updateCart",
    }),
    plus() {
      let count = this.item.quantity + 1;
      this.updateCart({ productID: this.item.product.id, startItem: count });
    },
    minus() {
      let count = this.item.quantity - 1;
      this.updateCart({ productID: this.item.product.id, startItem: count });
    },
  },
};
</script>

<style>
.button-del {
  cursor: pointer;
}
</style>