<template>
  <div class="content__top content__top--catalog">
    <h1 class="content__title">Каталог</h1>
    <span v-if="pagination" class="content__info">
      {{ pagination.total }} товаров
    </span>
  </div>
  <div class="content__catalog">
    <ProductFilter />

    <section id="catalog" class="catalog">
      <loader v-if="loader" />
      <ProductsList v-if="products.length != 0" :products="products" />
      <BaseEmpty class="catalog__empty" v-else />
      <BasePagination v-if="pagination" :pagination="pagination" />
    </section>
  </div>
</template>

<script>
import ProductsList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import loader from "@/components/UI/loader.vue";
import { mapActions, mapState } from "vuex";
import BaseEmpty from "@/components/BaseEmpty.vue";

export default {
  name: "MainPage",
  components: {
    ProductsList,
    BasePagination,
    ProductFilter,
    loader,
    BaseEmpty,
  },
  computed: {
    ...mapState({
      pagination: (state) => state.API.pagination,
      products: (state) => state.API.products,
      page: (state) => state.API.page,
      limit: (state) => state.API.limit,
      loader: (state) => state.API.loader,
    }),
    countProducts() {
      return this.products ? this.products.length : 0;
    },
  },
  methods: {
    ...mapActions({
      loadProducts: "API/loadProducts",
      loadCategories: "API/loadCategories",
      loadColors: "API/loadColors",
    }),
  },
  created() {
    this.loadProducts();
    this.loadCategories();
    this.loadColors();
  },
};
</script>

<style scoped lang="scss">
.catalog {
  position: relative;
  height: 100%;
  &__empty {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
  }
}
</style>
