import { createStore } from "vuex";
import { API } from "./module_API";

export default createStore({
    state: {

        cardProducts: [],
        contacts: {
            tel: '8 800 600 90 09',
        }
    },
    getters: {
        cardDetailsProduct(state) {
            return state.cardProducts.map(item => {
                const product = state.API.cartProductsData.find(p => p.product.id == item.product.id).product;
                return {
                    ...item,
                    product
                }
            })
        },
     
    },
    mutations: {

        pushCard(state, payload) {
            const item = state.cardProducts.find(item => item.product.id == payload.product.id);
            if (item) {
                item.count += payload.count;
            } else {
                state.cardProducts.push(payload)
            }

        },
        updateCardProductCount(state, { id, count }) {
            const item = state.cardProducts.find(item => item.product.id == id);
            if (item) {
                item.count = count;
            }
        },
        deleteProductCard(state, id) {

            state.cardProducts = state.cardProducts.filter(item => item.product.id !== id);
        },

    },
    actions: {

    },
    modules: {
        API: API
    }

})