<template>
  <form class="cart__form form" @submit.prevent="pushForm">
    <div class="cart__field">
      <div class="cart__data">
        <label class="form__label">
          <input
            class="form__input"
            type="text"
            name="name"
            placeholder="Введите ваше полное имя"
            v-model="form.fullName"
            @blur="v$.form.fullName.$touch"
          />
          <span class="form__value">ФИО</span>
          <span v-if="v$.form.fullName.$error" class="form__error"
            >Это поле обязательное</span
          >
        </label>

        <label class="form__label">
          <input
            class="form__input"
            type="text"
            name="address"
            placeholder="Введите ваш адрес"
            v-model="form.address"
            @blur="v$.form.address.$touch"
          />
          <span class="form__value">Адрес доставки</span>
          <span v-if="v$.form.address.$error" class="form__error"
            >Это поле обязательное</span
          >
        </label>

        <label class="form__label">
          <input
            class="form__input"
            type="tel"
            name="phone"
            placeholder="Введите ваш телефон"
            v-model="form.phone"
            @blur="v$.form.phone.$touch"
          />
          <span class="form__value">Телефон</span>
          <span v-if="v$.form.phone.$error" class="form__error"
            >Это поле обязательное</span
          >
        </label>

        <label class="form__label">
          <input
            class="form__input"
            type="email"
            name="email"
            placeholder="Введи ваш Email"
            v-model="form.email"
            @blur="v$.form.email.$touch"
          />
          <span class="form__value">Email</span>
          <span v-if="v$.form.email.$error" class="form__error"
            >Это поле обязательное</span
          >
        </label>

        <label class="form__label">
          <textarea
            class="form__input form__input--area"
            name="comments"
            placeholder="Ваши пожелания"
            v-model="form.message"
          ></textarea>
          <span class="form__value">Комментарий к заказу</span>
        </label>
      </div>

      <div class="cart__options">
        <h3 class="cart__title">Доставка</h3>
        <ul class="cart__options options">
          <li
            class="options__item"
            v-for="elemDelivery in delivery"
            :key="elemDelivery.code"
          >
            <label class="options__label">
              <input
                class="options__radio sr-only"
                type="radio"
                name="delivery"
                :checked="elemDelivery.active"
                :value="elemDelivery.price"
                @click="(event) => changeDelivery(event, elemDelivery)"
              />
              <span class="options__value">
                {{ elemDelivery.name }}
                <b>
                  {{
                    elemDelivery.price == 0
                      ? "бесплатно"
                      : elemDelivery.price + " ₽"
                  }}</b
                >
              </span>
            </label>
          </li>
        </ul>

        <h3 class="cart__title">Оплата</h3>
        <ul class="cart__options options">
          <li class="options__item" v-for="elemPay in pay" :key="elemPay.code">
            <label class="options__label">
              <input
                class="options__radio sr-only"
                type="radio"
                name="pay"
                :value="elemPay.code"
                :checked="elemPay.active"
                @click="(event) => changePay(event, elemPay)"
              />
              <span class="options__value">
                {{ elemPay.name }} при получении
              </span>
            </label>
          </li>
        </ul>
      </div>
    </div>

    <div class="cart__block">
      <ul class="cart__orders">
        <li
          class="cart__order"
          v-for="product in cartProductsData"
          :key="product.id"
        >
          <h3>{{ product.product.title }}</h3>
          <b>{{ product.product.price }} ₽</b>
          <span>Артикул: {{ product.product.id }}</span>
        </li>
      </ul>

      <div class="cart__total">
        <p>
          Доставка: <b>{{ namePrice }}</b>
        </p>
        <p>
          Итого: <b>{{ cartTotalCount }}</b> товара на сумму
          <b>{{ cartTotalPrice }} ₽</b>
        </p>
      </div>

      <button class="cart__button button button--primery" type="submit">
        Оформить заказ
      </button>
    </div>
    <div v-if="v$.$error" class="cart__error form__error-block">
      <h4>Заявка не отправлена!</h4>
      <p>
        Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите
        страницу.
      </p>
    </div>
  </form>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { useVuelidate } from "@vuelidate/core";
import { required, email } from "@vuelidate/validators";
export default {
  name: "CardForm",
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      pay: [
        {
          code: "card",
          name: "Картой",
          active: true,
        },
        {
          code: "cash",
          name: "Наличными",
          active: false,
        },
      ],
      form: {
        fullName: "тест",
        address: "тест",
        phone: "+79023844006",
        email: "test@test.com",
        message: "тест",
      },
    };
  },
  validations() {
    return {
      form: {
        fullName: { required },
        address: { required },
        phone: { required },
        email: { required, email },
      },
    };
  },
  computed: {
    ...mapState({
      cartProductsData: (state) => state.API.cartProductsData,
      orderId: (state) => state.API.order.id,
      delivery: (state) => state.API.delivery,
    }),
    ...mapGetters({
      cartTotalPrice: "API/cartTotalPrice",
      namePrice: "API/namePrice",
      cartTotalCount: "API/cartTotalCount",
    }),
  },
  methods: {
    ...mapActions({
      createOrder: "API/createOrder",
    }),
    changeDelivery(event, elemDelivery) {
      this.delivery.forEach((e) => (e.active = false));
      elemDelivery.active = true;
    },
    changePay(event, elemPay) {
      this.pay.forEach((e) => (e.active = false));
      elemPay.active = true;
    },
    async pushForm() {
      this.v$.$validate();
      if (!this.v$.$error) {
        alert("Форма Успешно отправленна");
        const order = await this.createOrder(this.form);
        this.$router.push(`/cart/orders/${order.data.id}`);
      }
    },
  },
};
</script>

