<template>
  <link v-if="/cart\/orders\/.*/.test($route.fullPath)" href="../../css/style.min.css" rel="stylesheet" />
  <link v-else href="../css/style.min.css" rel="stylesheet" />
  <main class="content container">
    <router-view />
  </main>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
export default {
  methods: {
    ...mapActions({
      loadCart: "API/loadCart",
      getAccessKey: "API/getAccessKey",
      getCart: "API/getCart",
    }),
  },
  created() {
    if (!localStorage.getItem("accessKey")) {
      this.getAccessKey();
    }
    this.getCart();
  },
};
</script>
<style>
#app {
  margin-bottom: auto;
}
</style>