<template>
  <div class="cart__form form">
    <div class="cart__field">
      <loader v-if="loader" />
      <ul v-else class="cart__list">
        <CardItem
          v-for="item in cartProductsData"
          :key="item.product.id"
          :item="item"
        />
      </ul>
    </div>
    <div class="cart__block">
      <p class="cart__desc">
        Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
      </p>
      <p class="cart__price">
        Итого: <span>{{ cartTotalPrice }} ₽</span>
      </p>

      <button
        v-if="cartProductsData.length != 0"
        @click.prevent="$router.push(`/cart/?delivery=true`)"
        class="cart__button button button--primery"
      >
        Оформить заказ
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from "vuex";
import loader from "@/components/UI/loader.vue";
import CardItem from "@/components/loop/CardItem.vue";
export default {
  name: "CardList",
  components: { CardItem, loader },
  computed: {
    ...mapState({
      cartProductsData: (state) => state.API.cartProductsData,
      loader: (state) => state.API.loader,
    }),
    ...mapGetters({
      products: "cardDetailsProduct",
    }),
    cartTotalPrice() {
      return this.cartProductsData.reduce(
        (e, el) => el.product.price * el.quantity + e,
        0
      );
    },
  },
};
</script>

