<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <div
        class="pagination__link pagination__link--arrow"
        aria-label="Предыдущая страница"
        @click="togglePage(currentPage - 1)"
        v-if="startpage != currentPage"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </div>
    </li>
    <li class="pagination__item" v-for="page in pagination.pages" :key="page">
      <div
        class="pagination__link"
        @click.prevent="togglePage(page)"
        :class="{ 'pagination__link--disabled': pagination.page != page }"
      >
        {{ page }}
      </div>
    </li>
    <li class="pagination__item" v-if="currentPage != pagination.pages">
      <div
        class="pagination__link pagination__link--arrow"
        @click="togglePage(currentPage + 1)"
        aria-label="Следующая страница"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </div>
    </li>
  </ul>
</template>
<!--  -->
<script>
import { mapActions, mapState } from "vuex";
export default {
  name: "Pagination",
  props: ["pagination"],
  data() {
    return {
      startpage: 1,
      currentPage: 1,
    };
  },
  methods: {
    ...mapActions({
      loadProducts: "API/loadProducts",
    }),
    togglePage(val) {
      this.currentPage = val;
      this.loadProducts({
        page: val,
      });
    },
  },
};
</script>

<style>
.pagination__link {
  cursor: pointer;
}
.pagination__item:last-child {
  margin: 0;
}
</style>