import { createApp } from 'vue';
import App from '@/App';
import router from '@/router/router';
import ToastPlugin from 'vue-toast-notification';
import HeaderComponent from '@/components/cross-site/HeaderComponent';
import FooterComponent from '@/components/cross-site/FooterComponent';
import store from '@/store';

createApp(HeaderComponent).use(router).use(store).mount('header')
createApp(App).use(router).use(store).use(ToastPlugin).mount('#app')
createApp(FooterComponent).use(router).use(store).mount('footer')

