import axios from "axios";
import { useToast } from "vue-toast-notification";
export const API = {
    namespaced: true,
    state: () => ({
        page: 1,
        limit: 6,
        colors: null,
        pagination: null,
        categories: null,
        products: [],
        productSingle: null,
        cartProductsData: [],
        loader: false,
        delivery: [
            {
                code: "delivery",
                name: "Самовывывоз",
                price: 0,
                active: true,
            },
            {
                code: "delivery",
                name: "Курьером",
                price: 500,
                active: false,
            },
        ],
    }),
    getters: {

        currentDilivery(state) {
            const current = state.delivery.filter(e => e.active == true);
            return current;
        },

        cartTotalPrice(state) {
            const deliveryPrice = state.delivery.filter((e) => e.active == true);

            return state.cartProductsData.reduce(
                (e, el) => el.product.price * el.quantity + e + deliveryPrice[0].price,
                0
            );
        },
        namePrice(state) {
            let nameElemPrice = "";
            state.delivery.forEach((e) => {
                if (e.name == "Самовывывоз" && e.active == true) {
                    nameElemPrice = "бесплатно";
                } else if (e.name == "Курьером" && e.active == true) {
                    nameElemPrice = e.price + " ₽";
                }
            });

            return nameElemPrice;
        },
        cartTotalCount(state) {
            return state.cartProductsData.reduce((e, el) => el.quantity + e, 0);
        },
    },
    mutations: {
        setProductsSingle(state, data) {
            state.productSingle = data;
        },
        setProducts(state, data) {
            state.products = data;
        },
        setProductsCategory(state, data) {
            state.categories = data;
        },
        setProductsColors(state, data) {
            state.colors = data;
        },
        setPagination(state, data) {
            state.pagination = data;
        },
        setCartProductsData(state, data) {
            state.cartProductsData = data;
        },
        setLoader(state, data) {
            state.loader = data;
        },
        setCardItems(state, data) {
            state.cartProductsData.push(data);
        },
        setOrderId(state, data) {
            state.order.id = data;
        },
        setOrder(state, data) {
            state.order.orderBody = data;
        },
        deleteCartProductsData(state, id) {
            state.cartProductsData = state.cartProductsData.filter(e => e.product.id != id);
        },
    },
    actions: {
        async loadProducts({ state, commit }, args = null) {
            commit('setLoader', true)

            const query = {
                page: state.page,
                limit: state.limit,
            };

            if (args) {
                if (args.color) query.colorId = args.color;
                if (args.minPrice) query.minPrice = args.minPrice;
                if (args.maxPrice) query.maxPrice = args.maxPrice;
                if (args.category) query.categoryId = args.category;
                if (args.limit) query.limit = args.limit;
                if (args.page) query.page = args.page;
            }
            const response = await axios.get(`https://vue-study.skillbox.cc/api/products`, { params: query })

            if (response.data.items.length != 0) {
                commit('setProducts', response.data.items)
                commit('setPagination', response.data.pagination)
            } else {
                commit('setProducts', [])
                commit('setPagination', false)
            }
            commit('setLoader', false)



        },
        async loadProduct({ state, commit }, id) {

            const response = await axios.get(`https://vue-study.skillbox.cc/api/products/${id}`);

            commit('setProductsSingle', response.data)
            commit('setLoader', false)
        },
        loadCategories({ state, commit }) {
            commit('setLoader', true)
            axios.get('https://vue-study.skillbox.cc/api/productCategories').then(response => {
                commit('setLoader', false)
                commit('setProductsCategory', response.data.items)
            })
        },
        loadColors({ state, commit }) {
            commit('setLoader', true)
            axios.get('https://vue-study.skillbox.cc/api/colors').then(response => {
                commit('setLoader', false)
                commit('setProductsColors', response.data.items)
            })
        },
        loadCart({ state, commit }, context) {
            commit('setLoader', true)
            axios.get('https://vue-study.skillbox.cc/api/baskets', {
                params: {
                    userAccessKey: localStorage.getItem("accessKey"),
                }

            }).then(response => {

                commit('setLoader', false)
                commit('setCartProductsData', response.data.items)
                commit('syncCartProducts')
            })
        },
        getAccessKey({ state, commit }) {
            axios.get('https://vue-study.skillbox.cc/api/users/accessKey').then(e => localStorage.setItem("accessKey", e.data.accessKey))
        },
        getCart({ state, commit }) {
            commit('setLoader', true)
            axios.get('https://vue-study.skillbox.cc/api/baskets', {
                params: {
                    userAccessKey: localStorage.getItem("accessKey"),
                }
            }).then(e => {
                commit('setCartProductsData', e.data.items)
                commit('setLoader', false)
            })
        },
        updateCart({ state, commit }, context) {
            axios.put(`https://vue-study.skillbox.cc/api/baskets/products?userAccessKey=${localStorage.getItem("accessKey")}`, {

                productId: context.productID,
                quantity: context.startItem,

                headers: { 'content-type': 'application/x-www-form-urlencoded' },
            }).then(e => commit('setCartProductsData', e.data.items))
        },
        deleteCart({ state, commit }, context) {
            axios.delete(`https://vue-study.skillbox.cc/api/baskets/products`, {
                params: {
                    userAccessKey: localStorage.getItem("accessKey")
                },
                data: {
                    productId: context.product.id,
                },
                headers: { 'content-type': 'application/x-www-form-urlencoded' },

            }).then(e => commit('deleteCartProductsData', context.product.id))
            console.log()
            useToast().error(`удален ${context.product.title}`)
        },
        async createOrder({ state, commit }, context) {
            commit('setLoader', true)
            const response = await axios.post(`https://vue-study.skillbox.cc/api/orders?userAccessKey=${localStorage.getItem("accessKey")}`, {
                headers: { 'content-type': 'application/x-www-form-urlencoded' },
                name: context.fullName,
                address: context.address,
                phone: context.phone,
                email: context.email,
                comment: context.message,
                pay: 1,
            })
            commit('setLoader', false)
            return response;
        },
        async getOrder({ state, commit }, context) {

            commit('setLoader', true)
            const response = await axios.get(`https://vue-study.skillbox.cc/api/orders/${context}`, {
                params: {
                    userAccessKey: localStorage.getItem("accessKey"),
                }
            })
            commit('setLoader', false)
            return response;
        },
        pushCard({ state, commit }, context) {
            commit('setLoader', true)
            axios.post(`https://vue-study.skillbox.cc/api/baskets/products?userAccessKey=${localStorage.getItem("accessKey")}`, {
                headers: { 'content-type': 'application/x-www-form-urlencoded' },

                productId: context.productID,
                quantity: context.startItem,
            }).then(e => {
                commit('setCartProductsData', e.data.items)
                commit('setLoader', false)
            })
            useToast().success(`Добавлено в корзину ${state.productSingle.title}`)

        }
    }
}

