<template>
  <main class="content container">
    <div class="content__top">
      <BaseBreadcrumbs :component="productSingle" />
    </div>
    <section class="item">
      <ProductGallery v-if="productSingle" :product="productSingle" />
      <ProductInfo v-if="productSingle" :product="productSingle" />
      <ProductDescr v-if="productSingle" :product="productSingle" />
    </section>
  </main>
</template>

<script>
import ProductDescr from "@/components/ProductPageComponents/ProductDescr.vue";
import ProductInfo from "@/components/ProductPageComponents/ProductInfo.vue";
import ProductGallery from "@/components/ProductPageComponents/ProductGallery.vue";
import BaseBreadcrumbs from "@/components/BaseBreadcrumbs.vue";

import { mapActions, mapState } from "vuex";

export default {
  name: "ProductPage",
  components: {
    ProductDescr,
    ProductInfo,
    ProductGallery,
    BaseBreadcrumbs,
  },
  computed: {
    ...mapState({
      productSingle: (state) => state.API.productSingle,
      loaderParam: (state) => state.API.loader,
    }),
  },
  methods: {
    ...mapActions({
      loadPorduct: "API/loadProduct",
    }),
  },
  created() {
    this.loadPorduct(this.$route.params.id);
  },
};
</script>

