<template>
  <BaseBreadcrumbs />

  <section v-if="orderResult" class="cart">
    <div class="cart__form form">
      <div class="cart__field">
        <p class="cart__message">
          Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
          письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
          в&nbsp;течение часа для уточнения деталей доставки.
        </p>

        <ul class="dictionary">
          <li class="dictionary__item">
            <span class="dictionary__key"> Получатель </span>
            <span class="dictionary__value">{{ orderResult.name }} </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key"> Адрес доставки </span>
            <span class="dictionary__value">{{ orderResult.address }} </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key"> Телефон </span>
            <span class="dictionary__value">{{ orderResult.phone }} </span>
          </li>
          <li class="dictionary__item">
            <span class="dictionary__key"> Email </span>
            <span class="dictionary__value"> {{ orderResult.email }} </span>
          </li>
          <!-- <li class="dictionary__item">
            <span class="dictionary__key"> Способ оплаты </span>
            <span class="dictionary__value"> картой при получении </span>
          </li> -->
        </ul>
      </div>

      <div class="cart__block">
        <ul class="cart__orders">
          <li
            class="cart__order"
            v-for="orderbasket in orderResult.basket.items"
            :key="orderbasket.id"
          >
            <h3>{{ orderbasket.product.title }}</h3>
            <b>{{ orderbasket.product.price }} ₽</b>
            <span>Артикул: {{ orderbasket.product.id }}</span>
          </li>
        </ul>

        <div class="cart__total">
          <p>
            Доставка: <b>{{ namePrice }}</b>
          </p>
          <p>
            Итого: <b>{{ cartTotalCount }}</b> товара на сумму
            <b>{{ orderResult.totalPrice }} ₽</b>
          </p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  name: "SuccessPage",
  data() {
    return {
      orderResult: null,
    };
  },
  computed: {
    ...mapState({
      delivery: (state) => state.API.productSingle,
    }),
    ...mapGetters({
      namePrice: "API/namePrice",
    }),
    cartTotalCount() {
      return this.orderResult.basket.items.reduce((e, el) => el.quantity + e, 0);
    },
  },
  methods: {
    ...mapActions({
      getOrder: "API/getOrder",
    }),
  },
  async created() {
    const response = await this.getOrder(this.$route.params.id);

    this.orderResult = response.data;
  },
};
</script>

